.videolist{
	display: inline-block;
	width: 35%;
	padding-left: 3rem; 
}
@media screen and (max-width: 650px){
	.videolist{
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
		padding-right: .5rem;
		width: 100%;
		padding-left: .5rem;
		-ms-flex-pack: justify;
		    justify-content: space-between;
	}
}


