.mediaplayer{
	display: inline-block;
	width: 65%;
	height: 65rem;
	-webkit-box-shadow: .3rem .3rem 2rem rgba(0,0,0,.5);
	        box-shadow: .3rem .3rem 2rem rgba(0,0,0,.5);
	margin-bottom: 3rem;
	border-radius: 2rem;
	background-color: rgba(30, 39, 46, .9);
}
.iframe{
	width: 100%;
	height: 40rem;
}
.main-channel{
	display: inline-block;
	padding: .7rem 2.4rem;
	border-radius: 2rem;
	background-color: #d63031;
	color: #fff;
	-ms-flex-item-align: center;
	    align-self: center;
	max-width: 15rem; 	
}
.main-title{
	display: -ms-flexbox;
	display: flex;
	margin-top: 2rem;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
.main-title h3{
	margin-right: 1rem;
}
.mediaplayer hr{
	width: 70%;
	display: inline-block;
}

.media-description{
	padding: 0 2rem;
}


@media screen and (max-width: 1000px){
	.iframe{
		height: 30rem;
	}
	.main-title{
		-ms-flex-direction: column;
		    flex-direction: column;
	}
	.main-channel{
		-ms-flex-item-align: start;
		    align-self: flex-start;
		margin-bottom: 1.5rem;	
	}
	.mediaplayer hr{
		width: 40%;
	}
	.mediaplayer{
		height: 60rem;
	}
}

@media screen and (max-width: 650px){
	.mediaplayer{
		width: 100%;
		height: 55rem;
	}
	.iframe{
		height: 27rem;
	}
}



